.table
  width: 100%
  height: 100%

  .theader
    padding: 10px 20px
    display: flex
    flex-direction: row
    align-items: center

    span
      color: #ffffff
      @media (min-width: 800px)
        font-size: 20px
      @media (max-width: 400px)
        font-size: 14px

    &__divider
      width: 2px
      background: transparent

  .tbody
    height: 82%
    min-height: 200px
    padding: 20px
    border-radius: 16px
    background: rgba(255, 255, 255, 0.71)

    &__content
      display: flex
      flex-direction: column
      overflow-y: auto
      width: 100%

      &::-webkit-scrollbar
        width: 9px

      &::-webkit-scrollbar-thumb
        background: rgba(255, 255, 255, 0.8)
        border-radius: 50px

      &::-webkit-scrollbar-track
        background: rgba(0, 0, 0, 0.4)
        border-radius: 50px

    &__row
      width: 100%
      display: flex
      flex-direction: row
      align-items: center
      position: relative
      &:not(span:last-child)
        margin: 10px 0

      span
        color: #556BAC

.table
  width: 100%

  .thead, .trow
    display: flex
    justify-content: space-between
    align-items: center

  .thead
    margin-bottom: 5px

  .tbody
    display: flex
    flex-direction: column
    justify-content: flex-start
    align-items: center
    padding: 15px 10px 15px 15px
    background-color: #ffffff
    border-radius: 10px

    .scroller
      max-height: 25vh
      width: 100%
      overflow-y: auto
      scrollbar-gutter: stable

      &::-webkit-scrollbar
        width: 5px
        height: 5px

      &::-webkit-scrollbar-thumb
        background: rgba(255, 255, 255, 0.9)
        border-radius: 50px

      &::-webkit-scrollbar-track
        background: rgba(0, 0, 0, 0.2)
        border-radius: 50px

  .trow
    width: 100%
    padding: 10px 10px 10px 5px
    align-items: center

  .trow:first-child
    padding-top: 0

  .trow:last-child
    padding-bottom: 0

  .tdata
    &_head
      color: #ffffff
      font-size: 10px
      @media (min-width: 800px)
        font-size: 24px
    &_body
      color: #DD4A38
      font-size: 12px
      @media (min-width: 800px)
        font-size: 24px
