.table {
  width: 100%;
  height: 100%;
}
.table .theader {
  padding: 10px 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.table .theader span {
  color: #ffffff;
}
@media (min-width: 800px) {
  .table .theader span {
    font-size: 20px;
  }
}
@media (max-width: 400px) {
  .table .theader span {
    font-size: 14px;
  }
}
.table .theader__divider {
  width: 2px;
  background: transparent;
}
.table .tbody {
  height: 82%;
  min-height: 200px;
  padding: 20px;
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.71);
}
.table .tbody__content {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  width: 100%;
}
.table .tbody__content::-webkit-scrollbar {
  width: 9px;
}
.table .tbody__content::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.8);
  border-radius: 50px;
}
.table .tbody__content::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.4);
  border-radius: 50px;
}
.table .tbody__row {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
}
.table .tbody__row:not(span:last-child) {
  margin: 10px 0;
}
.table .tbody__row span {
  color: #556BAC;
}

.table {
  width: 100%;
}
.table .thead, .table .trow {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.table .thead {
  margin-bottom: 5px;
}
.table .tbody {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 15px 10px 15px 15px;
  background-color: #ffffff;
  border-radius: 10px;
}
.table .tbody .scroller {
  max-height: 25vh;
  width: 100%;
  overflow-y: auto;
  scrollbar-gutter: stable;
}
.table .tbody .scroller::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
.table .tbody .scroller::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.9);
  border-radius: 50px;
}
.table .tbody .scroller::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 50px;
}
.table .trow {
  width: 100%;
  padding: 10px 10px 10px 5px;
  align-items: center;
}
.table .trow:first-child {
  padding-top: 0;
}
.table .trow:last-child {
  padding-bottom: 0;
}
.table .tdata_head {
  color: #ffffff;
  font-size: 10px;
}
@media (min-width: 800px) {
  .table .tdata_head {
    font-size: 24px;
  }
}
.table .tdata_body {
  color: #DD4A38;
  font-size: 12px;
}
@media (min-width: 800px) {
  .table .tdata_body {
    font-size: 24px;
  }
}

